*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #504945;
  background-color: #7fa2ac;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue;
  line-height: 1.5;
}

img {
  object-fit: cover;
  border-radius: 2px;
  display: block;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.loader {
  width: 100%;
  height: 12px;
  box-sizing: border-box;
  background-color: #fff;
  background-image: linear-gradient(45deg, rgba(0, 0, 0, .25) 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, .25) 50%, rgba(0, 0, 0, .25) 75%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0));
  background-size: 1em 1em;
  font-size: 30px;
  animation: barStripe 1s linear infinite;
  display: inline-block;
}

@keyframes barStripe {
  0% {
    background-position: 1em 0;
